@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

button {
    @include dimensions(90%, _);
    @include setMargin(5px, 5px);
    @include setPadding(10px, 0);
    @include displayFlex(center, center, column);

    border-radius: 2px;
    transition-duration: 200ms;

    &:focus {
        outline: 0;
    }
}

.light-theme {
    .filter-button {

        @include dimensions(94%, _);
        color: $white-1;
        border: 1px solid $blue-1;
        background: $blue-1;

        &:hover {
            cursor: pointer;
            background: $blue-2;
            border-color: $blue-2;
        }
    }

    .change-theme {
        border: 1px solid rgb(58, 58, 58);
        color: rgb(58, 58, 58);
        background: transparent;
        cursor: pointer;
    }

    .selected {
        background: $blue-1;
        border-color: $blue-1;
        color: $white-1;
        height: 25px;
        font-size: 13.8px;

        &:hover {
            background-color: $blue-2;
            border-color: $blue-2;
            color: $white-1;
        }
    }

    .unselected {
        background: transparent;
        border-color: $light-theme-1;
        color: $light-theme-3;
        height: 25px;
        font-size: 13.8px;

        &:hover {
            background: $blue-1;
            border-color: $blue-1;
            color: $white-1;
        }
    }
}

.dark-theme {
    .filter-button {

        @include dimensions(94%, _);

        color: $white-1;
        border: 1px solid $blue-1;
        background-color: $blue-1;

        &:hover {
            cursor: pointer;
            background: $blue-2;
            border-color: $blue-2;
        }
    }

    .change-theme {
        border: 1px solid rgb(207, 207, 207);
        color: rgb(207, 207, 207);
        background: transparent;
        cursor: pointer;
    }

    .selected {
        background: $blue-1;
        border-color: $blue-1;
        color: $white-1;
        height: 25px;
        font-size: 13.8px;

        &:hover {
            background-color: $blue-2;
            border-color: $blue-2;
            color: $white-1;
        }
    }

    .unselected {
        color: $dark-theme-4;
        border-color: $dark-theme-8;
        height: 25px;
        font-size: 13.8px;

        &:hover {
            background: $blue-1;
            border-color: $blue-1;
            color: $white-1;
        }
    }
}