@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

.dark-theme{
    .range-container{

        @include dimensions(94%, _);
        @include displayFlex(center, center, row);

        position: relative;
        margin: 3px 0;

        .range-title{
            
            @include dimensions(38%, _);
            
            color: rgb(207, 207, 207);
            font-weight: 400;
            text-align: right;
            margin-right: 5px;
            font-size: 13px;
            pointer-events: none;
        }

        .range-value{
            @include setMargin(3px, 0);
            @include dimensions(17%, _);

            color: rgb(207, 207, 207);
            font-weight: 400;
            text-align: right;
            margin-right: 5px;
            font-size: 13px;
            pointer-events: none;
        }

        input {
            @include setMargin(3px, 0);
            @include dimensions(45%, 5px);

            background: rgb(83, 83, 83);
            border-radius: 10px;
            -webkit-appearance: none;
            transition: 100ms;

            &:hover{
                background: rgb(116, 116, 116);
            }

            &:focus{
                outline: 0;
            }
            &::-webkit-slider-thumb{
                -webkit-appearance: none;
                background-color: $blue-1;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                cursor: pointer;
                transition: 200ms;
                
                &:hover{
                    background-color: $blue-3;
                    transform: scale(1.15);
                }
            }
        }          
    }
}

.light-theme{
    .range-container{

        @include dimensions(94%, _);
        @include displayFlex(center, center, row);

        position: relative;
        margin: 3px 0;

        .range-title{
            
            @include dimensions(38%, _);
            
            color: rgb(58, 58, 58);
            font-weight: 500;
            text-align: right;
            margin-right: 5px;
            font-size: 13px;
            pointer-events: none;
        }

        .range-value{
            @include setMargin(3px, 0);
            @include dimensions(17%, _);

            color: rgb(58, 58, 58);
            font-weight: 500;
            text-align: right;
            margin-right: 5px;
            font-size: 13px;
            pointer-events: none;
        }

        input {
            @include setMargin(3px, 0);
            @include dimensions(45%, 5px);

            background: rgb(175, 175, 175);
            border-radius: 10px;
            -webkit-appearance: none;
            transition: 200ms;

            &:hover{
                background: rgb(150, 150, 150);
            }

            &:focus{
                outline: 0;
            }
            &::-webkit-slider-thumb{
                -webkit-appearance: none;
                background-color: rgb(0, 116, 232);
                width: 12px;
                height: 12px;
                border-radius: 50%;
                cursor: pointer;
                transition: 200ms;
                
                &:hover{
                    background-color: rgb(0, 116, 232);
                    transform: scale(1.15);
                }
            }
        }
          
    }
}