@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

#map {
    @include dimensions(77%, 100%);
    @include noSpaces( );
}

.leaflet-popup-content-wrapper{
    border-radius: 2px;
    background: rgb(252, 252, 252);
}

.leaflet-popup-content{
    div{
        @include displayFlex(_, _, column);
    
        div{

            @include displayFlex(flex-start,_ , row);
            
            span:first-child{
                font-weight: 600;
                margin-right: 8px;
                color: rgb(65, 65, 65);
            }

            span:last-child{
                color: rgb(92, 92, 92);
            }

            a{
                color: $text-color-button;
                background: $blue-3;
                width: 100%;
                text-decoration: none;
                border-radius: 2px;
                margin-top: 5px;
                padding: 3.5px 0;
                text-align: center;
                transition: background-color 300ms;

                &:hover{
                    color: $text-color-button-hover;
                    background: $blue-1;
                }
            }
        }
    }
}

.leaflet-popup-tip{
    border-radius: 2px;
    background: rgb(252, 252, 252);
}

.marker-cluster-small{

    background: $marker-cluster-small;
    opacity: 1;
    
    &:hover{
            background: $marker-cluster-small-hover;
        }

    div{
        background: $marker-cluster-small;
        transition: background-color 170ms;

        span{
            font-weight: 700;
            color: rgb(255, 255, 255);
        }
    }
}

