@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

.select-container{
    @include dimensions(94%, _);
    @include displayFlex(center, center, row);

    position: relative;

    .select-title{

        @include dimensions(53%, _);

        text-align: right;
        margin-right: 5px;
        font-size: 13.2px;
        pointer-events: none;
    }

    input{
        @include setMargin(3px, 0);
        @include setPadding(9px, 5px);
        @include dimensions(57%, _);

        font-size: 14.5px;
        border-radius: 2px;
        transition-duration: 200ms;
        border-width: 1px;
        border-style: solid;
        cursor: pointer;

        &:focus{
            outline: none;
        }
    }

    .options-dropdown{
        @include dimensions(53%, _);
        @include displayFlex(center, center, column);

        position: absolute;
        top: calc(100% - 2px);
        right: 0;
        z-index: 10;
        transition: opacity 300ms;

        span{
            @include setPadding(5px, 5px);
            
            width: calc(100% - 10px);
            font-size: 14.5px;
            text-align: center;
            border-bottom-width: 1px;
            border-bottom-style: solid;
            cursor: pointer;
        }
    }
}

.dark-theme{
    .select-container{
        .select-title{
            color: $dark-theme-5;
        }
    
        input{
            color: $dark-theme-5;
            background: $input-dark-theme;
            border-color: $input-dark-theme;
    
            &:hover{
                border-color: $blue-2;
            }
            &:focus{
                border-color: $blue-2;
            }
        }
    
        .options-dropdown{    
            span{
                border-color: $dark-theme-2;
                color: $dark-theme-5;
                background: $input-dark-theme;
    
                &:hover{
                    color: $dark-theme-6;
                    background: $blue-1;
                }
            }
        }
    }
}

.light-theme{
    .select-container{
        .select-title{
            color: $light-theme-4;
            font-weight: 500;
        }
    
        input{
            color: $light-theme-4;
            border-color: $light-theme-1;
            
            &:hover{
                border-color: $blue-3;
                background: $white-2;
            }
        }
    
        .options-dropdown{    
            span{
                background: $white-1;
                border-bottom-color: $light-theme-1;
                border-left: 1px solid $light-theme-1;
                border-right: 1px solid $light-theme-1;
                color: $light-theme-4;
    
                &:hover{
                    background: $blue-3;
                    color: $white-1;
                }
            }
            span:first-child{
                border-top: 1px solid $light-theme-1;
            }
        }
    }            
}

.hide{ opacity: 0; pointer-events: none; }