@import '../../../../utils/mixins/Mixins.scss';
@import '../../../../utils/vars/vars.scss';

.filters-header{
    @include dimensions(100%, _);
    @include setPadding(15px, 0);

    h1{
        margin: 0;
        padding: 0 0 5px 15px;
        font-weight: 400;
        font-size: 23px;
        pointer-events: none;
    }

    a{
        font-size: 13px;
        margin: 0;
        padding: 0 0 0 15px;
        text-decoration: none;
        transition: color 300ms;
    }
}

.light-theme{
    .filters-header{

        background: $white-1;
        border: none;
        box-shadow: 0 1px 3px rgba(102, 102, 102, 0.055), 0 1px 2px rgba(121, 121, 121, 0.075);

        h1{
            color: $light-theme-4;
        }
        a{
            color: $light-theme-3;

            &:hover{
                color: $light-theme-4;
            }
        }
    }
}

.dark-theme{
    .filters-header{
        background: $dark-theme-0;
        box-shadow: 2px 1px 4px 1px $dark-theme-1;

        h1{
            color: $dark-theme-6;
        }
        a{
            color: $dark-theme-5;

            &:hover{    
                color: $dark-theme-6;
            }
        }
    }
}