@import '../../utils/mixins/Mixins.scss';

.map-container {
    overflow: hidden;
    position: relative;

    .show-menu{
        position: absolute;
        background: navy;
        color: #fff;
        padding: 5px;

        top: 10px;
        width: 100px;
        left: 65%;
        z-index: 100;
    }
    
    @include noSpaces( );
    @include dimensions(100%, 100%);
    @include displayFlex(center, center, row);
}

.main-container {
    overflow: hidden;
    position: relative;
    
    @include noSpaces( );
    @include dimensions(100vw, 100vh);
    @include displayFlex(center, center, column);
}