@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');

@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

$date-title-color-dark: rgb(207, 207, 207);
$input-color-dark: rgb(216, 216, 216);
$input-bg-color-dark: rgb(30, 30, 30);
$input-bg-color-hover-dark: rgba(26, 26, 26, 0.89);
$input-border-color-hover-dark: rgb(0, 116, 232);

$date-title-color-light: rgb(58, 58, 58);
$input-color-light: rgb(58, 58, 58);
$input-border-color-light: rgb(194, 194, 194);
$input-bg-color-light: transparent;
$input-border-light: 1px solid rgb(194, 194, 194);
$input-border-color-hover-light: rgb(0, 116, 232);
$input-bg-color-hover-light: rgb(236, 238, 250);

input[type=date]::-webkit-inner-spin-button,
input[type=date]::-webkit-outer-spin-button {
    -webkit-appearance: none;
}

input[type=date]::-webkit-calendar-picker-indicator {        
    -webkit-appearance: none;
    opacity: 0;
    background-color: transparent;
        
    &:hover{
        opacity: 1
    }
}

.date-container{
    @include dimensions(94%, _);
    @include displayFlex(center, center, row);

    position: relative;

    .date-title{
        @include dimensions(53%, _);

        font-weight: 400;
        text-align: right;
        margin-right: 5px;
        font-size: 13px;
        pointer-events: none;
    }

    input[type=date]{
        font-family: 'Roboto', sans-serif;
        @include setMargin(3px, 0);
        @include setPadding(7px, 5px);
        @include dimensions(57%, _);

        appearance: none;
        border-width: 1px;
        border-style: solid;
        border-radius: 2px;
        font-size: 14.5px;
        transition-duration: 200ms;
        cursor: pointer;

        &:focus{
            outline: none;
        }
    }
}

.dark-theme{
    .date-container{
        .date-title{
            color: $dark-theme-5; 
        }

        input[type=date]{
            color: $dark-theme-5;
            border-color: $input-dark-theme;
            background: $input-dark-theme;
            
            &:hover{
                border-color: $blue-1;
            }
            &::selection{
                background: $blue-1;
            }
        }
    }
}

.light-theme{
    .date-container{
        .date-title{
            color: $light-theme-4;
            font-weight: 500;
        }

        input[type=date]{
            color: $light-theme-4;
            border-color: $light-theme-1;
            
            &:hover{
                border-color: $blue-3;
                background: $white-2;
            }

            &::selection{
                background-color: $blue-3;
            }
        }
    }
}