@import '../../../utils/mixins/Mixins.scss';
@import '../../../utils/vars/vars.scss';

#filters-menu {
    @include displayFlex(center, space-between, column);

    height: 100%;
    max-width: 40%;
    min-width: 23%;
    position: relative;
    overflow-Y: auto;
    overflow-X: hidden;

    .resizer {
        position: absolute;
        width: 8px;
        background: $blue-1;
        right: calc(0% - 2px);
        top: 0;
        opacity: 0;
        z-index: 1000;
        transition: opacity 150ms;
        cursor: col-resize;

        &:hover {
            opacity: .3;
        }
    }
}

.light-theme {

    background: #F5F6F8;

    &::-webkit-scrollbar {
        width: 7px;
    }

    &::-webkit-scrollbar-track {
        background: $light-theme-2;
    }

    &::-webkit-scrollbar-thumb {
        background: $light-theme-1;
        border-radius: 2px;

        &:hover{
            background: $light-theme-6;
        }
    }
}

.dark-theme {
    background: $dark-theme-1;

    &::-webkit-scrollbar {
        width: 7px;
    }

    &::-webkit-scrollbar-track {
        background: $dark-theme-0;
    }

    &::-webkit-scrollbar-thumb {
        background: $dark-theme-9;
        border-radius: 2px;

        &:hover{
            background: $dark-theme-10;
        }
    }
}