@import '../../../../utils/mixins/Mixins.scss';
@import '../../../../utils/vars/vars.scss';

.filters-footer{
    @include dimensions(100%, _);
    @include setPadding(15px, 0);

    margin: 10px 0 0 0;

    .user-info{
        @include displayFlex(center, flex-start, row);
        @include dimensions(calc(100% - 30px), _);

        margin: 5px 0 5px 15px;
        padding-bottom: 15px;
        border-bottom-width: 1px;
        border-bottom-style: solid;

        .username{
            font-size: 16px;
            margin-right: 8px;
        }
        .logout{
            padding: 2px 4px;
            font-size: 12.5px;
            transition: 300ms;
            text-decoration: none;
            cursor: pointer;
            
            border-width: 1px;
            border-style: solid;
            border-radius: 2px;
        }
    }

    .version{
        margin: 15px 0 5px 15px;
        font-size: 12px;
        font-weight: 500;
        pointer-events: none;

        span{
            margin-left: 1px;
            font-size: 14px;
        }
    }
}

.dark-theme{
    .filters-footer{
        background: $dark-theme-0;
        
        .user-info{

            border-bottom-color: $dark-theme-8;
            
            .username{
                color: $dark-theme-6;
            }
            .logout{
                color: $dark-theme-4;
                border-color: $dark-theme-8;
                
                &:hover{
                    border-color: $dark-theme-5;
                    color: $dark-theme-5;
                }
            }
        }
        .version{
            color: $dark-theme-4;
        }
    }
}

.light-theme{
    .filters-footer{
        
        background: $white-1;
        border: none;
        box-shadow: 0 -5px 1px 3px rgba(102, 102, 102, 0.055), 0 1px 2px rgba(121, 121, 121, 0.075);
        
        .user-info{

            border-bottom-color: $light-theme-1;
            
            .username{
                color: $light-theme-4;
            }
            .logout{
                color: $light-theme-3;
                border-color: $light-theme-2;
                
                &:hover{
                    border-color: $light-theme-4;
                    color: $light-theme-4;
                }
            }
        }
        .version{
            color: $light-theme-4;
        }
    }
}