@import '../../utils/mixins/Mixins.scss';
@import '../../utils/vars/vars.scss';

.login{
    @include dimensions(100%, 100%);
    @include noSpaces();

    position: relative;
    overflow: hidden;
    
    img{
        @include dimensions(30px, 30px);
    }

    * {
        @include noSpaces()
    }

    header {

        min-width: 100vw;
        width: 100vw;
        padding: 5px 0;
        background: #20232a;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        z-index: 10;

        a {
            margin: auto 40px;
            text-decoration: none;
            @include displayFlex(center, center, row);

            img{
                width: 50px;
                height: 50px;
                margin: 4px 0 0 0;
                padding: 0;
            }
        
            h1{
                margin: 0;
                padding: 0;
                font-weight: 400;
                font-size: 30px;
                color: #f7f7f7;
            }

            &:last-child {
                font-size: 17px;
                color: $dark-theme-6;
                border: 1px solid $blue-3;
                background: $blue-3;
                border-radius: 2px;
                padding: 10px 0;
                width: 100px;
                transition: all 200ms;

                &:hover {
                    border-color: $blue-1;
                    background: $blue-1;
                }
            }

            &:focus{
                outline: 0;
            }
        }
    }

    .login-content{
        @include dimensions(100%, calc(100vh - 65px));
        @include displayFlex(center, center, column);

        position: relative;

        .no-auth{
            position: absolute;
            pointer-events: none;
            text-align: center;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            margin-top: 10px;
            font-size: 26px;
            background: rgba(209, 33, 33, 0.829);
            border: 1px solid rgba(209, 33, 33, 0.925);
            border-radius: 2px;
            padding: 30px 20px;
            font-weight: 400;
            color: #fff;
            z-index: 100000;
            transition: 300ms;
        }
    
        .hide-message {
            opacity: 0;
        }
    }
}