@mixin setPadding($vertical, $horizontal) {
    padding: $vertical $horizontal;
}

@mixin setMargin($vertical, $horizontal) {
    margin: $vertical $horizontal;
}

@mixin noSpaces {
    margin: 0;
    padding: 0;
}

@mixin displayFlex($align, $justify, $direction) {
    display: flex;

    @if $align { align-items: $align }
    @else{ align-items: center; }

    @if $justify { justify-content: $justify }
    @else{ justify-content: center; }

    @if $direction { flex-direction: $direction }
    @else{ flex-direction: column; }
}

@mixin dimensions($width, $height) {
    @if $width { width: $width }
    @else { width: 100% }

    @if $height { height: $height }
    @else { height: 100% }
}